.header .modal {
  display: inline-flex;
}
.header .not-modal {
  padding-right: 20px;
  @include mq(mobile-only) {
    display: none;
  }
  @include mq(tablet) {
    margin-bottom: 0px;
    margin-right: 0px;
    margin-left: 32px;
    min-width: 160px;
  }
  @include mq(desktop) {
    order: 1;
    min-width: 159px;
    margin-left: 0px;
  }
}
.header__buy-button {
  padding-right: 20px;
  display: inline-flex;
  justify-content: center;

  background-repeat: no-repeat;
  background-size: 10px 10px;
  background-position: right 18px top 19px;
  padding: -right 40px;
  font-family: 'DM Sans', 'Sans-serif';
  font-weight: 700;
  font-size: 16px;
  line-height: calc(21 / 16);
  align-items: center;
  text-align: center;
  min-height: 44px;
  border-radius: 22px;
  color: var(--red-text-color);
  border-color: transparent;
  background-color: var(--white-text-color);
  background-image: url('../images/hero/arrow-2.svg');
  background-repeat: no-repeat;
  transition-property: background-color;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 250ms;
  cursor: pointer;
  &:focus,
  &:hover {
    color: var(--white-text-color);
    background-color: var(--red-text-color);
    background-image: url('../images/hero/arrow.svg');
    background-repeat: no-repeat;
  }
  @include mq(mobile-only) {
    min-width: 209px;
    margin-bottom: 19px;
  }
  @include mq(tablet-only) {
    min-width: 184px;
    margin-bottom: 20px;
  }
  @include mq(desktop) {
    background-position: right 18px top 17px;
  }
}
@include mq(desktop) {
  .header .modal,
  .hero__button-paragraph {
    display: none;
  }
}
.hero__button-link {
  color: var(--red-text-color);
  transition-property: color;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 250ms;
}
.hero__button-list {
  display: flex;
}
.hero__button-item:not(:last-child) {
  margin-right: 8px;
  @include mq(desktop) {
    margin-right: 12px;
  }
}
.hero__button {
  background-color: var(--white-text-color);
  color: var(--red-text-color);
  min-width: 62px;
  max-width: 82px;
  min-height: 24px;
  border: transparent;
  border-radius: 22px;
  font-family: 'DM Sans', 'Sans-serif';
  font-weight: 400;
  transition-property: background-color;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 250ms;
  &:focus,
  &:hover {
    background-color: var(--red-text-color);
  }
  &:focus .hero__button-link,
  &:hover .hero__button-link {
    color: var(--white-text-color);
  }
  @include mq(not-desktop) {
    font-size: 8px;
    line-height: calc(10 / 8);
    padding: 5px, 10px;
  }
  @include mq(desktop) {
    min-width: 107px;
    max-width: 138px;
    min-height: 30px;
    font-size: 14px;
    line-height: calc(18 / 14);
    padding: 10px;
  }
}
@include mq(tablet) {
  .hero__button-paragraph {
    display: flex;
    fill: var(--bgc_second-btn-color);
    background-color: var(--white-text-color);
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    width: 18px;
    height: 18px;
    border: none;
    transition-property: background-color;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 250ms;
    @include mq(desktop) {
      width: 26px;
      height: 26px;
      left: 229px;
      bottom: 180px;
    }
    &:hover,
    &:focus {
      fill: #fff;
      background-color: var(--bgc_second-btn-color);
    }
  }
  .hero__button-paragraph-icon {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.hero__button-paragraph {
  @include mq(mobile-only) {
    display: none;
  }
}
